import React, { useEffect, useRef, useState } from "react";
import "../styles/Contacts.css";

const Contacts = ({ contactList, triggerOpenContactMessages }) => {
  const [selectedContact, setSelectedContact] = useState(null);

  useEffect(()=> {
    if (selectedContact) {
      triggerOpenContactMessages(selectedContact);
    }
  }, [selectedContact])

  const getBackgroundColor = (contact) => {
    if (selectedContact) {
      if (selectedContact.sender === contact.sender) {
        return "#b2bcec";
      } else {
        return "#f9f9f9";
      }
    }
    return "#f9f9f9";
  }

  return (
    <div className="contact-menu">
      {contactList.map((contact, index) => (
        <div key={index + contact.sender} className="contact" onClick={() => setSelectedContact(contact)} style={{ backgroundColor: getBackgroundColor(contact)}}>
          <div className="avatar">
            {contact.senderName.charAt(0).toUpperCase()}
          </div>
          <div className="contact-info">
            <div className="contact-name">{contact.senderName}</div>
            <div className="contact-number">{contact.sender}</div>
          </div>
        </div>
      ))}
    </div>
  );
};

export default Contacts;
