import React from 'react';
import '../styles/XMLViewer.css';

const XMLViewer = ({ data }) => {
  if (!data || data.length === 0) {
    return <p>No SMS data to display.</p>;
  }

  return (
    <div className="chat-container">
      <div className='contact-details'>
        <div className="avatar">{data[0].senderName.charAt(0).toUpperCase()}</div>
        <div>{data[0].senderName}</div>
        <div>{data[0].senderName ? "("+data[0].sender+ ")": data[0].sender}</div>
      </div>
      
      {data.map((msg, index) => (
        <div
          key={index}
          className={`chat-message ${msg.isUserMessage() ? 'user' : 'other'}`}
        >
          <div className="chat-bubble">
            <div className="chat-header">
              <span className="sender">{msg.senderName}</span>
              <span className="timestamp">{msg.getFormattedTime()}</span>
            </div>
            <div className="chat-text">{msg.body}</div>
          </div>
        </div>
      ))}
    </div>
  );
};

export default XMLViewer;
