import React, { useEffect, useState } from "react";
import FileUploader from "./components/FileUploader";
import XMLViewer from "./components/XMLViewer";
import SMSMessage from "./models/SMSMessage";
import { parseString } from "xml2js";
import "./styles/App.css";
import Contacts from "./components/Contacts";

const App = () => {
  const [messages, setMessages] = useState([]);
  const [search, setSearch] = useState("");
  const [contactList, setContacts] = useState([]);
  const [selectedContact, setSelectedContact] = useState(null);
  const [contactSpecificMessage, setContactSpecificMessage] = useState([])

  const handleFileUpload = (xmlString) => {
    parseString(xmlString, { explicitArray: false }, (err, result) => {
      if (err) {
        alert("Error parsing XML");
      } else {
        const rawMessages = result?.smses?.sms || [];
        const parsed = Array.isArray(rawMessages)
          ? rawMessages.map((msg) => new SMSMessage(msg))
          : [new SMSMessage(rawMessages)];
        setMessages(parsed);
      }
    });
  };

  useEffect(() => {
    if (messages && messages.length > 0) {
      const uniqueContactsMap = new Map();

      messages.forEach((msg) => {
        if (!uniqueContactsMap.has(msg.sender)) {
          uniqueContactsMap.set(msg.sender, {
            sender: msg.sender,
            senderName: msg.senderName,
          });
        }
      });

      const uniqueContacts = Array.from(uniqueContactsMap.values());
      setContacts(uniqueContacts);
    }
  }, [messages]);

  const filteredMessages = messages.filter(
    (msg) =>
      msg.body.toLowerCase().includes(search.toLowerCase()) ||
      msg.senderName.toLowerCase().includes(search.toLowerCase())
  );

  const triggerOpenContactMessages = (contact) => {
    setSelectedContact(contact);
  }

  useEffect(() => {
    if (selectedContact) {
      let filtered = messages.filter(msg => msg.sender === selectedContact.sender);
      setContactSpecificMessage(filtered);
    }
  }, [selectedContact])

  return (
    <div className="app-container">
      <h1>📱 SMS Viewer</h1>
      <FileUploader onFileUpload={handleFileUpload} />
      {messages.length > 0 && (
        <input
          type="text"
          placeholder="Search messages or contacts..."
          value={search}
          onChange={(e) => setSearch(e.target.value)}
          className="search-input"
        />
      )}
      {messages.length > 0 && <Contacts contactList={contactList} triggerOpenContactMessages={triggerOpenContactMessages} />}
      
      {contactSpecificMessage && contactSpecificMessage.length > 0 && (
        <XMLViewer data={contactSpecificMessage} />
      )}
      
    </div>
  );
};

export default App;
