export default class SMSMessage {
  constructor(xmlNode) {
    const attrs = xmlNode.$ || {};
    this.sender = attrs.address || 'Unknown';
    this.senderName = attrs.contact_name || this.sender;
    this.timestamp = parseInt(attrs.date, 10);
    this.readableDate = attrs.readable_date || '';
    this.body = attrs.body || '';
    this.type = attrs.type; // "1" = received, "2" = sent
  }

  getFormattedTime() {
    if (this.readableDate) return this.readableDate;
    const date = new Date(this.timestamp);
    return isNaN(date)
      ? 'Unknown time'
      : date.toLocaleString('en-IN', {
          dateStyle: 'medium',
          timeStyle: 'short',
        });
  }

  isUserMessage() {
    return this.type === '2';
  }

  getContactKey() {
    return this.sender;
  }
}

